.tabs{
    display: block;
    width: 100%;
    list-style: none;
}

.tab__list{
    display: flex;
    flex-flow: row;
    grid-gap: 2px;
    gap: 2px;
    border-bottom: 5px solid white;
}
.tab__item{
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px;
    background:  #d7dbdd ;
    overflow: hidden;
    text-align: center;
    color: black;
    cursor: pointer;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    transition: all 1s ease 0s ;
}

.tab__item--active{
    background:#2ecc71;
    box-shadow:#2ecc71 0px 0px 5px 0px;
}
.timer {
    background:  rgba(0, 0, 0, 0.5) ;
    color: white;
    font-size: 40px;
    text-align: center;
    width: 300px;
    border-radius: 10px;
}

.timer--breakTime{
    background:  rgba(52, 152, 219, 0.8);
}
.timer--workTime{
    background:  rgba(46, 204, 113, 0.5);
}
.timer > p{
    padding: 50px;
}

.timer__button{
    padding: 10px;
    background:#2ecc71;
    box-shadow:#2ecc71 0px 0px 5px 0px;
    border: none;
    font-size: 30px;
    border-radius: 10px;
    display: block;
    margin: auto;
    cursor:pointer
}
.timer__button--active{
    background:#2ecc706c;
    box-shadow:#2ecc706c 0px 0px 5px 0px;
}
@-webkit-keyframes moveDashboard {
    0%{
        margin-top: -50%;
    }
    100%{
        margin-top: 0%;
    }
}

@keyframes moveDashboard {
    0%{
        margin-top: -50%;
    }
    100%{
        margin-top: 0%;
    }
}

.dashboard {
    background:#ecf0f1  ;
    padding: 10px;
    box-shadow: white 5px 5px 10px 0px;
    border-radius: 15px;
    width: 50%;
    height: 400px;
    display: block;
    margin: auto;
    overflow: hidden;
    -webkit-animation: forwards 1s moveDashboard;
            animation: forwards 1s moveDashboard;
}

@media only screen and (max-width: 768px){
    .dashboard{
        width: 90%;
    }
}
.checkbox{
    padding: 5px;
    overflow: hidden;
    object-fit: cover;
    width: 100px;
    cursor: pointer;
    border-radius: 15px;
}

.checkbox--checked{
    background:#2ecc71;
}

.time__image{
    object-fit: cover;
    width: 100px;
}

.group {
    margin: auto;
    text-align: center;
}
.footer{
    width: 100%;
    display: block;
    margin-top: -14px;
    overflow: hidden;
}

.footer__col{
    display: flex;
    flex-flow: column;
}

.list__items{
    width: 100%;
    list-style: none;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    grid-gap: 20px;
    gap: 20px;
    text-align: center;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.item{
    padding: 10px;
}

.item > a{
    color: white;
}

.item > a:hover{
    color: #2ecc71;
    border-bottom: white solid 2px;
}

@media only screen and (max-width: 768px){
    .footer{
        display: block;
        margin-top: 0;
        width: 80%;
    }
    .list__items{
        flex-flow: column;
        width: 100%;
    }
}
.container{
    display: block;
    width: 100%;
    height: 100%;
}

@-webkit-keyframes moveLogo {
    0%{
        margin-left: -100%;
    }
    100%{
        margin-left: 0;
    }
}

@keyframes moveLogo {
    0%{
        margin-left: -100%;
    }
    100%{
        margin-left: 0;
    }
}

.logo-group{
    -webkit-animation: forwards 3s moveLogo ;
            animation: forwards 3s moveLogo ;
}
.logo{
    display: block;
    margin: auto;
    text-align: center;
    width: 100px;
    object-fit: cover;
}

.logo__title{
    text-align: center;
    color: white;
    font-size: 20px;
}

.row{
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    justify-content: center;
}

.row--h3{
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    height: 50px;
}

.col{
    display: flex;
    flex-flow: column;
    flex-wrap: wrap;
}

body {
  margin: 0;
  padding: 0;
  background: #d66d75; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right,  #ff6347 ,  #df4f4f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

